.profile {
  background-color: #ffffff;
  height: calc(100vh - 74px);
  font-family: "Inter", "Arial", "Helvetica", sans-serif;
  display: flex;
  flex-grow: 1;
}

.profile__container {
  display: none;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  height: 605px;
}

.profile__info-container {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
}

.profile__title {
  margin: 74px 0 123px 0;
  padding: 0;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.21;
  letter-spacing: 0em;
  color: #000000;
}

.profile__error-message {
  display: block;
  max-width: 396px;
  margin: 0;
  color: #ff3055;
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0em;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}


.profile__info-subtitle {
  font-size: 11px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.18;
  letter-spacing: 0px;
  margin: 0;
}
.profile__info-block {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 410px;
}

.profile__container_opened {
  display: flex;
}

.profile__break_line {
  width: 410px;
  height: 1px;
  background-color: #e8e8e8;
  margin: 16px 0 17px 0;
}

.profile__input {
  border: 0;
  outline: 0;
  text-align: right;
  width: 80%;
}
.profile__input_type_inactive {
  pointer-events: none;
}

@media screen and (max-width: 510px) {
  .profile__info-block {
    width: 260px;
  }
  .profile__break_line {
    width: 260px;
  }
  .profile__info-subtitle {
    font-weight: 500;
  }
  .profile__input {
    font-weight: 400;
  }
}

@media screen and (max-width: 410px) {
  .profile__title {
    margin: 68px 0 80px 0;
  }
  .profile__container {
    height: 715px;
  }
}
