.page-not-found {
  width: 100vw;
  height: 100vh;
  background-color: #ffffff;
  display: none;
  font-family: "Inter", "Arial", "Helvetica", sans-serif;
  color: #000000;
}

.page-not-found_opened {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-not-found__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.page-not-found__title {
  font-size: 140px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.2;
  letter-spacing: 0em;
  margin: 3px 0 8px 0;
}

.page-not-found__subtitle {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.19;
  letter-spacing: 0em;
  margin: 0 0 184px 0;
}

.page-not-found__link {
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.21;
  letter-spacing: 0em;
  color: #3456f3;
  border: 0;
  background-color: #ffffff;
  padding: 0;
}

@media screen and (max-width: 450px) {
  .page-not-found__title {
    font-size: 80px;
    line-height: 1.21;
  }
  .page-not-found__subtitle {
    font-size: 12px;
    line-height: 1.25;
  }
}
